import React from "react";
import { withRouter, Link } from "react-router-dom";
import Scene from './Scene';

import Artyom from 'artyom.js';
const Jarvis = new Artyom();

class Confirmation extends React.Component{

    constructor (props, context){
        super(props, context);
  
        // Add `this` context to the handler functions    
        this.Continue = this.Continue.bind(this);

  
        // Prepare simple state
        this.state = {
            User: "",
            isDictating:false
        };
        // Load some commands to Artyom using the commands manager
        
    }

    texto = "hola";
    videoPath = "";
    componentWillMount(){
        if(localStorage.getItem('emotion') == "tristeza"){
            this.texto = ", lamento que sientas "+ localStorage.getItem('emotion');
            this.videoPath = require("../videos/tristeza1.mp4");
        }

        if(localStorage.getItem('emotion') == "rabia"){
            this.texto = ", lamento que sientas "+ localStorage.getItem('emotion');
            this.videoPath = require("../videos/rabia1.mp4");
        }

        if(localStorage.getItem('emotion') == "confusión"){
            this.texto = ", lamento que sientas "+ localStorage.getItem('emotion');
            this.videoPath = require("../videos/confusion1.mp4");
        }

        if(localStorage.getItem('emotion') == "frustración"){
            this.texto = ", lamento que sientas "+ localStorage.getItem('emotion');
            this.videoPath = require("../videos/frustracion1.mp4");
        }

        if(localStorage.getItem('emotion') == "agradecimiento"){
            this.texto = ", me alegra que sientas "+ localStorage.getItem('emotion');
            this.videoPath = require("../videos/agradecimiento1.mp4");
        }

        if(localStorage.getItem('emotion') == "felicidad"){
            this.texto = ", me alegra que sientas "+ localStorage.getItem('emotion');
            this.videoPath = require("../videos/felicidad1.mp4");
        }
    }

    //let User = "";
    //const history = useHistory(); 

    Continue(value) {
        value? this.props.history.push('/seleccion'): this.props.history.push('/hola')
    }

    
    //

    render(){
        return ( 
            
            

            <div className="container-fluid text-center my-container" >
                <h1 style={{fontSize:"2vw",  height:"10%"}} className="font-weight-bold">{localStorage.getItem('name') + this.texto }</h1>
                {/*<small id="ayudacedula" className="form-text text-muted">Espero que después de nuestra conversación te sientas aún mejor.</small>*/}
                <div className="my-info-container">
    
                    <div className="my-form-container">
                        <p className="h4 mb-5">¿Quieres contarme sobre qué tema específico quieres hablar?</p>
                        <form>
                            <div className="form-group jd-flex flex-column ">
                                <div className="row jd-flex justify-content-center"> 
                                    <Link className="btn btn-success mb-5 col-sm-3" to={{
                                        pathname:'/seleccion'}} style={{textDecoration:'none'}}>Si</Link>
                                    <div className="col-sm-1"></div>
                                    <Link className="btn btn-danger mb-5 col-sm-3" to={{
                                        pathname:'/'}} style={{textDecoration:'none'}}>No, me quiero ir</Link>  
                                </div>               
                            </div>
                        </form> 
                    </div>

                    <div className="my-video-container">
                        <Scene videoPath={this.videoPath}></Scene>
                    </div>
                    
                </div>
                
                
            </div> 
                
        );
    }

    
};

export default withRouter(Confirmation)

